  font-size: 24px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.hero-section__guarantee {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 18px;
  background: rgba(246, 95, 95, 0.1);
  border-radius: 100px;
  width: 190px;
  padding: 8px;
}
.hero-section__guarantee--text {
  margin-bottom: 0;
  color: #f9f9f9;
  color: var(--color-primary-light);
}
.hero-section__icon {
  fill: #d0810b;
  fill: var(--color-brand-yellow-dark);
}
.hero-section__buttons {
  display: flex;
  gap: 20px;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .hero-section__buttons {
    flex-direction: row;
    gap: 29px;
  }
}
.hero-section__button {
  background: #819b57;
  background: var(--color-brand-normal);
  border: 2px solid #819b57;
  border: 2px solid var(--color-brand-normal);
}
.hero-section__button:hover {
  color: #465929;
  color: var(--color-brand-dark);
}
.hero-section__video-btn {
  font-weight: 500;
  font-size: 18px;
  display: flex;
  gap: 10px;
  cursor: pointer;
  align-items: center;
  border-radius: 50px;
  padding: 0 10px 0 5px;
}
.hero-section__video-btn:hover {
  background: rgba(131, 154, 169, 0.17);
  background: var(--color-secondary-hover);
}
.hero-section__btn {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background: #ff9f0e;
  background: var(--color-brand-yellow-light);
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero-section__icon-btn {
  fill: #010F1C;
}

.info__video {
  width: 100%;
  margin-bottom: 20px;
  height: auto;
  border-radius: 10px;
  border: 2px solid #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.info__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0;
  grid-row-gap: 0;
  grid-gap: 20px;
  gap: 20px;
}
.info__item {
  display: flex;
  align-items: center;
  gap: 10px;
}
.info__icon {
  fill: #819b57;
  fill: var(--color-brand-normal);
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 80%;
  height: 100%;
  padding: 16px 24px;
  background-color: #f9f9f9;
  background-color: var(--color-primary-light);
  transform: translateX(125%);
  transition: transform 1s;
  transition-timing-function: ease;
}
.mobile-menu.is-open {
  transform: translateX(25%);
}
.mobile-menu__btn-close {
  position: absolute;
  top: 16px;
  right: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  fill: transparent;
  stroke: rgb(33, 36, 41);
  stroke: var(--color-primary-dark);
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.mobile-menu__logo {
  margin-bottom: 32px;
}
.mobile-menu__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.mobile-menu__link {
  color: rgb(33, 36, 41);
  color: var(--color-primary-dark);
  font-size: 14px;
  line-height: 1.5;
  text-decoration: none;
}
.mobile-menu__link:hover {
  color: #819b57;
  color: var(--color-brand-normal);
}

.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  z-index: 2;
  transition: opacity 0.3s, visibility 0.3s;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .backdrop {
    padding: 20px;
  }
}
.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  position: relative;
  width: 100%;
  height: 100%;
  max-height: 880px;
  background-color: #f9f9f9;
  background-color: var(--color-primary-light);
  padding: 44px 34px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  overflow-y: auto;
}
@media screen and (min-width: 768px) {
  .modal {
    padding: 64px 48px;
    max-width: 1400px;
    border-radius: 32px;
    flex-direction: row;
  }
}
.modal__btn-close {
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  fill: transparent;
  stroke: rgb(33, 36, 41);
  stroke: var(--color-primary-dark);
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .modal__btn-close {
    top: 40px;
    right: 40px;
  }
}
.modal__img {
  height: 505px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .modal__img {
    height: auto;
  }
}
.modal__description {
  width: 100%;
  align-items: center;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .modal__description {
    width: 50%;
    align-items: start;
    gap: 20px;
  }
}
.modal__title {
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: 900;
  line-height: 1.5;
}
@media screen and (min-width: 768px) {
  .modal__title {
    font-size: 24px;
    line-height: 1.3;
  }
}
.modal__code {
  color: #839aa9;
  color: var(--color-secondary-text);
}
.modal__buttons {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}
.modal__counter {
  display: flex;
  border: 1px solid #777;
  border: 1px solid var(--color-secondary-border);
  border-radius: 5px;
  padding: 10px;
  align-items: center;
}
.modal__value {
  background: transparent;
  border: none;
  text-align: center;
  width: 30px;
  height: 30px;
  font-size: 16px;
}
.modal__value:focus-visible {
  outline: none;
}
.modal__plus-minus {
  border: none;
  background: transparent;
  font-size: 16px;
  cursor: pointer;
}
.modal__order {
  background-color: #ff9f0e;
  background-color: var(--color-brand-yellow-light);
  border: 2px solid #ff9f0e;
  border: 2px solid var(--color-brand-yellow-light);
}
.modal__order:hover {
  color: #d0810b;
  color: var(--color-brand-yellow-dark);
}
.modal__size-title {
  margin: 0;
  font-size: 28px;
}
.modal__size-list {
  display: flex;
  flex-direction: column;
  align-items: start;
  border: none;
  padding-left: 0;
  align-self: start;
}
.modal__size-item {
  display: flex;
  gap: 5px;
  align-items: center;
}
.modal__text {
  margin-bottom: 40px;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .modal__text {
    margin-bottom: 72px;
  }
}

.delivery__list {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .delivery__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0;
    grid-row-gap: 0;
  }
}
@media screen and (min-width: 992px) {
  .delivery__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.sale__prop {
  display: flex;
  justify-content: center;
  gap: 60px;
}
.sale__price {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-weight: 700;
  position: relative;
  padding: 10px 50px;
}
.sale__old {
  font-size: 24px;
  text-decoration: line-through;
}
.sale__new {
  font-size: 30px;
}
.sale__promotion {
  position: absolute;
  top: 0;
  right: 0;
}
.sale__button {
  background: #819b57;
  background: var(--color-brand-normal);
  border: 2px solid #819b57;
  border: 2px solid var(--color-brand-normal);
}
.sale__button:hover {
  color: #465929;
  color: var(--color-brand-dark);
}
.sale__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}
.sale__second-title {
  text-align: center;
  font-size: 30px;
  font-weight: 600;
}

.section {
  padding: 20px 0;
}
@media screen and (min-width: 768px) {
  .section {
    padding: 48px 0;
  }
}
.section__title {
  font-size: 32px;
  font-weight: 900;
  line-height: 1.5;
}
@media screen and (min-width: 768px) {
  .section__title {
    font-size: 64px;
    line-height: 1.3;
  }
}
.section__title--centered {
  text-align: center;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  color: rgb(33, 36, 41);
  color: var(--color-primary-dark);
  background-color: #f9f9f9;
  background-color: var(--color-primary-light);
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
  margin: 0;
  font-family: "Poppins", sans-serif;
}
@media screen and (min-width: 768px) {
  body {
    font-size: 18px;
    font-weight: 400;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  -o-object-fit: cover;
     object-fit: cover;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
}

address {
  font-style: normal;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  -webkit-appearance: none;
          appearance: none;
  -moz-appearance: textfield;
}

input[type=radio]:checked::before {
  transform: scale(1);
