: ease-in;\n  }\n  37% {\n    transform: translateX(5px);\n    timing-function: ease-out;\n  }\n  55% {\n    transform: translateX(-5px);\n    timing-function: ease-in;\n  }\n  73% {\n    transform: translateX(4px);\n    timing-function: ease-out;\n  }\n  82% {\n    transform: translateX(-4px);\n    timing-function: ease-in;\n  }\n  91% {\n    transform: translateX(2px);\n    timing-function: ease-out;\n  }\n  96% {\n    transform: translateX(-2px);\n    timing-function: ease-in;\n  }\n  100% {\n    transform: translateX(0px);\n    timing-function: ease-in;\n  }\n}\n.basket {\n  display: flex;\n  flex-direction: column;\n  gap: 20px;\n  width: 100%;\n  border-top: 1px solid rgba(0, 0, 0, 0.2);\n  border-bottom: 1px solid rgba(0, 0, 0, 0.2);\n}\n.basket__item {\n  width: 100%;\n  display: flex;\n  gap: 20px;\n  align-items: center;\n  justify-content: space-between;\n}\n.basket__img {\n  width: 70px;\n  height: 90px;\n  border-radius: 7px;\n}\n@media screen and (min-width: 768px) {\n  .basket__img {\n    width: 90px;\n    height: 120px;\n  }\n}\n.basket__info {\n  width: 90%;\n  display: flex;\n  flex-direction: column;\n  padding: 15px 0;\n  gap: 8px;\n}\n@media screen and (min-width: 600px) {\n  .basket__info {\n    align-items: center;\n    flex-direction: row;\n    padding: 0;\n  }\n}\n.basket__description {\n  width: 90%;\n  padding: 5px 0;\n  font-size: 16px;\n  display: flex;\n  gap: 15px;\n  align-items: center;\n}\n@media screen and (min-width: 600px) {\n  .basket__description {\n    padding: 15px 0;\n    width: 60%;\n  }\n}\n.basket__text {\n  display: flex;\n  flex-direction: column;\n  gap: 5px;\n}\n.basket__name {\n  margin-bottom: 0;\n  font-size: 14px;\n}\n@media screen and (min-width: 768px) {\n  .basket__name {\n    font-size: 16px;\n  }\n}\n.basket__options {\n  opacity: 0.7;\n  font-size: 12px;\n  font-weight: 400;\n  margin: 0;\n}\n.basket__settings {\n  display: flex;\n  height: 100%;\n  gap: 15px;\n  margin-left: 90px;\n  align-items: center;\n}\n@media screen and (min-width: 600px) {\n  .basket__settings {\n    margin-left: 0;\n    width: 40%;\n    justify-content: end;\n  }\n}\n.basket__counter {\n  display: flex;\n  gap: 5px;\n  flex-wrap: nowrap;\n}\n.basket__value {\n  width: 26px;\n  height: 30px;\n  border: none;\n  text-align: center;\n  align-items: center;\n  display: flex;\n  justify-content: center;\n  background: transparent;\n}\n.basket__value:focus-visible {\n  outline: none;\n}\n.basket__plus-minus {\n  width: 30px;\n  height: 30px;\n  background: transparent;\n  border: 1px solid var(--color-secondary-border);\n  border-radius: 50%;\n  align-items: center;\n  justify-content: center;\n  display: flex;\n  padding: 0;\n  color: var(--color-secondary-border);\n  cursor: pointer;\n}\n.basket__price {\n  text-wrap: nowrap;\n  margin: 0;\n  width: 80px;\n}\n.basket__delete {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  fill: transparent;\n  stroke: var(--color-secondary-border);\n  background-color: transparent;\n  padding: 5px;\n  border: none;\n  cursor: pointer;\n}\n.basket__form {\n  margin-bottom: 30px;\n}\n.basket__final-sum {\n  display: flex;\n  justify-content: end;\n  color: #000;\n  gap: 5px;\n}\n.basket__final-sum--total {\n  font-size: 22px;\n}\n.basket__sum {\n  font-size: 16px;\n}\n.basket__submit {\n  background-color: var(--color-brand-normal);\n  border: 2px solid var(--color-brand-normal);\n  width: 100%;\n}\n.basket__submit:hover {\n  color: var(--color-brand-dark);\n  border: 2px solid var(--color-brand-dark);\n}\n\n.button {\n  border-radius: 8px;\n  padding: 16px 32px;\n  border: none;\n  cursor: pointer;\n  font-size: 16px;\n  font-weight: 600;\n  color: var(--color-primary-light);\n  font-family: \"Poppins\", sans-serif;\n}\n.button:hover {\n  background: transparent;\n}\n\n.card {\n  border-radius: 8px;\n  box-shadow: 0 8px 16px 0 rgba(24, 24, 23, 0.08);\n  background: #fff;\n  overflow: hidden;\n  cursor: pointer;\n  width: 100%;\n}\n.card__container {\n  padding: 15px;\n}\n.card__image {\n  height: 408px;\n}\n.card__title {\n  font-size: 16px;\n  font-weight: 400;\n  text-align: center;\n  height: 48px;\n  margin-bottom: 10px;\n}\n.card__buttons {\n  display: flex;\n  gap: 5px;\n  justify-content: center;\n}\n.card__button-detail {\n  background-color: var(--color-brand-normal);\n  border: 2px solid var(--color-brand-normal);\n}\n.card__button-detail:hover {\n  color: var(--color-brand-dark);\n}\n.card__button-buy {\n  background-color: var(--color-brand-yellow-light);\n  border: 2px solid var(--color-brand-yellow-light);\n}\n.card__button-buy:hover {\n  color: var(--color-brand-yellow-dark);\n}\n\n.input {\n  position: relative;\n}\n.input__label {\n  position: absolute;\n  top: 16px;\n  left: 42px;\n}\n.input__label-order {\n  color: var(--color-secondary-dark);\n}\n.input__control {\n  padding: 8px 24px;\n  width: 100%;\n  font-size: 16px;\n  line-height: 1.17;\n  border-radius: 8px;\n  border: 1px solid var(--color-secondary);\n  background-color: transparent;\n}\n.input__control:focus {\n  border-color: var(--color-brand-normal);\n  outline: none;\n}\n.input__control::placeholder {\n  color: var(--color-secondary);\n  font-size: 16px;\n  line-height: 1.17;\n}\n.input__control:focus::placeholder {\n  color: var(--color-primary-dark);\n}\n@media screen and (min-width: 768px) {\n  .input__control {\n    padding: 16px 42px;\n  }\n}\n\n.link {\n  color: var(--color-primary-light);\n  text-decoration: none;\n  transition: color 0.3s;\n}\n.link:hover {\n  color: var(--color-brand-normal);\n}\n\n.order__form-field {\n  margin-bottom: 24px;\n  display: flex;\n  flex-direction: column;\n  gap: 5px;\n  color: var(--color-secondary-dark);\n}\n.order__delivery-list {\n  border: none;\n  padding-left: 0;\n  color: var(--color-secondary-dark);\n}\n.order__selected {\n  display: flex;\n  flex-direction: column;\n  gap: 5px;\n  color: var(--color-secondary-dark);\n  margin-bottom: 24px;\n}\n.order__payment {\n  padding: 16px 42px;\n  width: 100%;\n  font-size: 16px;\n  line-height: 1.17;\n  border-radius: 8px;\n  border: 1px solid var(--color-secondary);\n  background-color: transparent;\n}\n.order__payment:focus {\n  border-color: var(--color-brand-normal);\n  outline: none;\n}\n.order__options {\n  color: var(--color-secondary-dark);\n}\n.order__options:hover {\n  background-color: red;\n}\n\n.delivery-card {\n  align-items: center;\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n}\n.delivery-card__img {\n  width: 233px;\n  height: 195px;\n  background-image: url(\"../../public/images/delivery.png\");\n  background-size: contain;\n  background-repeat: no-repeat;\n  background-position: center;\n}\n.delivery-card__description {\n  text-align: center;\n  width: 233px;\n}\n.delivery-card__title {\n  font-size: 28px;\n  font-weight: 700;\n  margin-bottom: 10px;\n}\n\n.gallery {\n  position: relative;\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n@media screen and (min-width: 768px) {\n  .gallery {\n    width: 50%;\n  }\n}\n.gallery__slides {\n  display: none;\n}\n.gallery__prev, .gallery__next {\n  cursor: pointer;\n  position: absolute;\n  top: 40%;\n  width: auto;\n  padding: 16px;\n  margin-top: -50px;\n  color: white;\n  font-weight: bold;\n  font-size: 20px;\n  border-radius: 0 3px 3px 0;\n  user-select: none;\n  -webkit-user-select: none;\n}\n.gallery__next {\n  right: 0;\n  border-radius: 3px 0 0 3px;\n}\n.gallery__prev:hover, .gallery__next:hover {\n  background-color: rgba(0, 0, 0, 0.8);\n}\n.gallery__row {\n  justify-content: center;\n  display: flex;\n  gap: 10px;\n}\n.gallery__row:after {\n  content: \"\";\n  display: table;\n  clear: both;\n}\n.gallery__column {\n  float: left;\n  width: 16.66%;\n}\n.gallery__demo {\n  opacity: 0.6;\n  width: 100%;\n  cursor: pointer;\n}\n.gallery__active, .gallery__demo:hover {\n  opacity: 1;\n}\n\n.price {\n  display: flex;\n  gap: 8px;\n  justify-content: center;\n  align-items: cente